*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

*::-webkit-scrollbar {
  width: 18px;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #999999;
  border-radius: 15px;
  border: 5px solid #fff;
}

html {
  font-size: 62.5%;
  font-family: 'Roboto', sans-serif;
}

body {
  box-sizing: border-box;
}

a { 
  text-decoration: none; 
}

*:focus, *:hover, *:visited, *:link, *:active {
  text-decoration: none;
  color: #606060;
}

/* HEADER */
.header {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  height: 56px;
  padding: 0 16px;
}

.header__search, .header__icons, .header__navigation {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
}

.header__search {
  padding: 5px;
}

.header__search input {
  width: 38vw;
  height: 32px;
  border: 1px solid #ccc;
  border-right: none;
  border-radius: 2px 0 0 2px;
  box-shadow: inset 0 1px 2px #f2f2f2;
  font-size: 16px;
  padding: 1rem;
}

.header__search input:focus{
  outline: none;
}

.header__search--icon {
  border: 1px solid #ccc;
  padding: 4px 22px 3px 22px;
  color: rgba(0, 0, 0, 0.64);
  background-color: #f8f8f8;
  border-radius: 0 2px 2px 0;
  cursor: pointer;
}

.header__menu {
  color: rgba(0, 0, 0, 0.60) !important;
  cursor: pointer;
  margin-right: 25px;
  margin-left: .5rem;
  padding: 3px;
}

.youtube__icon {
  width: 80px;
  cursor: pointer;
}

.header__icons-icon {
  padding-right: 2rem;
  color: rgba(0, 0, 0, 0.60) !important;
  cursor: pointer;
}

.header__avatar {
  margin-right: 1.4rem;
  width: 35px !important;
  height: 35px !important;
  cursor: pointer;

}

/* MAIN CONTENT */
.main {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

/* SIDEBAR */
.sidebar.active {
  flex: .18;
  padding-top: 1rem;
}

.sidebar.hidden {
  display: none;
}

.sidebar__item {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  padding: .2rem 0 .5rem 2.5rem;
  cursor: pointer;
}

.sidebar__item:hover {
  background-color: #f2f2f2;
}

.sidebar__icon {
  font-size: 28px !important;
  margin-right: 2.5rem;
  padding: 3px;
  color: rgba(0, 0, 0, 0.64);
}

.sidebar__border {
  background-color: #ccc;
  width: 100%;
  height: .05rem;
  margin: 12px 0;
}

.sidebar__title {
  color: #030303;
  font-weight: 400;
  font-size: 1.4rem;
}

.sidebar__subhead {
  text-transform: uppercase;
  color: rgba(96, 96, 96, .7);
  margin-bottom: 1.2rem;
  padding-left: 2.5rem;
  font-size: 1.5rem;
  font-weight: 500;
}

.sidebar__item.selected {
  background-color: #e9e9e9;
}

.sidebar__item.selected > .sidebar__title {
  font-weight: 500;
}

.sidebar__item.selected > .sidebar__icon {
  color: red;
}

/* VIDEO CONTAINER */
.videos {
  background-color: #f9f9f9;
}

.videos.active {
  flex: .82;
}

.videos.active {
  flex: 1;
}

.videos__border {
  background-color: #ccc;
  width: 100%;
  height: .05rem;
  margin: 12px 0;
}

.video__item {
  cursor: pointer;
}

/* SEARCH VIDEOS */
.search__videos {
  padding: .2rem 5rem;
}

.search__videos .video__item {
  padding: 1.2rem;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.search__videos .video__info {
  padding: 0 1.2rem;
  margin-right: 5rem;
}

.search__videos .video__container img {
  width: 360px;
  height: 202px;
}

.search__videos .video__item h2 {
  font-size: 1.6rem;
  font-weight: 400;
  padding-bottom: .4rem;
}

.search__videos .video__publish, .search__videos .video__channel, .search__videos .video__description {
  font-size: 1.3rem;
  line-height: 1;
  color: #606060;
}

.search__videos .video__channel, .search__videos .video__description {
  padding-top: 1.3rem;
}

/* SUGGESTED VIDEOS */
.suggest__videos {
  padding: 2rem .5rem;
}

.suggested__videos .video__item h2 {
  font-size: 1.4rem;
  line-height: 1;
  font-weight: 500;
  padding: 1rem 0;
}

.videos .suggested__header {
  margin: 0 2rem;
  font-size: 1.5rem;
  font-weight: 500;
}

.suggested__videos .video__publish, .suggested__videos .video__channel {
  font-size: 1.4rem;
  line-height: 1;
  color: #606060;
  padding: .2rem 0;
}

.suggested__videos .video__info {
  width: 250px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.suggest__avatar {
  padding: .8rem 1rem 0 0;
}

.suggested__videos  {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.suggested__videos .video__item {
  margin-bottom: 4rem;
}

.suggested__videos .video__item img {
  width: 277px;
  height: 158px;
}

/* VIDEO PLAYER */
.sidebar__container .header__navigation {
  padding: 1.4rem 0 0 1.6rem;
}

.sidebar__container.active {
  display: block;
}

.sidebar__container.hidden {
  display: none;
}

.sidebar__container {
  width: 230px;
  min-height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  background-color: #fff;
}

.video__player {
  display: grid;
  grid-template-rows: 100%;
  grid-template-columns: repeat(2, 1fr);
  padding: 0 10rem;
  grid-column-gap: 2rem;
  position: relative;
}

.player__container {
  grid-row: 1 / 2;
  grid-column: 1 / 2;
  justify-self: end;
}

.player__info {
  grid-row: 1 / 2;
  grid-column: 2 / 3;
  justify-self: start;
}

.player__info img {
  width: 50px;
  height: 50px;
}


.player__recommend {
  flex: .15;
}

.player__recommend .suggest__avatar {
  display: none;
}

.player__frame iframe  {
  width: 900px;
  height: 500px;
  padding-top: 1.5rem;
}

.video__avatar {
  display: none;
}

.video__player .video__item {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.video__player .video__container img {
  height: 95px;
  width: 170px;
  padding-top: 1rem;
}

.video__player .suggested__videos .video__item {
  margin-bottom: .5rem;
}

.video__player .video__info {
  width: 220px;
  padding-left: .8rem;
}

.video__player .video__info p {
  font-size: 1.3rem;
}

.video__player .video__info h2 {
  color: #030303;
}

.video__player .videos__border {
  width: 80%;
  height: .07rem;
}

.player__info--stats {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  color: #606060;
}

.player__info--right {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}

.player__icons {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  padding: .5rem 0 .5rem 2.5rem;
  cursor: pointer;
}

.player__icon {
  color: #606060;
}

.player__title {
  padding: 0 .5rem;
  font-size: 1.4rem;
  font-weight: 400;
}

.player__info h2 {
  font-size: 1.6rem;
  font-weight: 400;
  color: #030303;
  padding: 1.5rem 0;
}

.player__info--left {
  font-size: 1.4rem;
  font-weight: 400;
}

.player__border {
  width: 100%;
  background-color: #e9e9e9;
  height: .1rem;
}

.player__info--channel {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  margin-right: 30%;
}

.player__info--avatar {
  padding-right: 1.6rem;
  padding-top: 2rem;
}


.channel__name {
  color: #030303;
  font-size: 1.4rem;
  font-weight: 500;
  padding-top: 2rem;
}

.channel__subs {
  color: #606060;
  padding: .2rem 0;
  font-size: 1.3rem;
}

.player__description {
  padding: 2rem 0;
  font-size: 1.4rem;
}